import { createRouter, createWebHistory } from 'vue-router';
import Pika from '../components/Home/HelloWorld.vue';
import DateWizard from '../components/Tool/DateWizard/DateWizard.vue'

const routes = [
  {
    path: '/',
    name: 'Pika',
    component: Pika,
    meta: {
        korean: '따옴표🧙‍♂️'
    }
  },
  {
    path: '/DateWizard',
    name: 'DateWizard',
    component: DateWizard,
    meta: {
        korean: '날짜🧙‍♂️'
    }
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});

export default router;