<template>
  <div style="margin: 15px;">
    <el-row :gutter="20">
      <el-col :span="24">
        <h4>기준일</h4>
        <div style="margin-bottom: 20px;">
          <el-radio-group v-model="rdoValType" @change="changeRadio">
            <el-radio-button label="PROC_YM" value="procym" />
            <el-radio-button label="PROC_DATE" value="procdate" />
            <el-radio-button label="날짜" value="date" />
          </el-radio-group>
        </div>
        <el-date-picker
          v-if="rdoValType === 'date'"
          v-model="input1"
          type="date"
          value-format="YYYY-MM-DD"
          placeholder="YYYY-MM-DD"
          ></el-date-picker>
        <el-input
          v-if="rdoValType !== 'date'"
          v-model="input1"></el-input>

        <el-button type="success" :icon="Refresh" style="margin-bottom: 20px;" @click="refresh"></el-button>
      </el-col>
    </el-row>
    <el-row>
      <div class="mb-4">
        <el-button type="primary" round @click="changeDate('투캐릭터')">TO_CHAR</el-button>
        <el-button type="primary" round @click="changeDate('투데이트')">TO_DATE</el-button>
        <el-button type="primary" round @click="changeDate('1일투데이트')">1일</el-button>
        <el-button type="primary" round @click="changeDate('말일')">말일</el-button>
        <el-button type="primary" round @click="changeDate('1일시분초')">1일시분초</el-button>
        <el-button type="primary" round @click="changeDate('시분초')">시분초</el-button>
        <el-button type="primary" round @click="changeDate('애드먼스')">ADD_MONTHS</el-button>
        <el-button type="primary" round @click="changeDate('막대기없애기')">막대기없애기</el-button>
        <el-button type="primary" round @click="changeDate('트렁크')">TRUNC</el-button>
      </div>
    </el-row>
    <el-row>
      <el-col :span="2">
        <div>
          <el-button type="primary" :icon="ArrowLeftBold" circle @click="undo"> </el-button>
          <el-button type="primary" circle @click="copyToClipboard">
            <el-icon>
              <component :is="copiedStates ? 'Select' : 'DocumentCopy'" />
            </el-icon>
          </el-button>
        </div>
        </el-col>
      <el-col :span="22">
        <div
          style="border-radius:var(--el-border-radius-base);
                border: 1px solid var(--el-border-color);
                word-wrap: break-word;
                overflow-wrap: break-word;
                padding: 10px;">
          <p v-html="resultValue"></p>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        {{ changeHistoryKR.join(' >') }}
      </el-col>
    </el-row>
  </div>
</template>

<script setup>
import { ref, watch } from 'vue'
import { ArrowLeftBold, DocumentCopy, Refresh } from '@element-plus/icons-vue'
// import PROC_YM from './DateWizard_ProcYM.vue'
// import PROC_DATE from './DateWizard_ProcDate.vue'
import {format} from 'date-fns'
import DateUtil from '@/utils/dateUtil'
import { ElMessage } from 'element-plus'

const rdoValType = ref('procym') //라디오버튼
const input1 = ref('P_PROC_YM') //기준값
const today = new Date()
const resultValue = ref(input1.value)
const resultText = ref(input1.value)
const changeHistory = ref([])
const changeHistoryKR = ref([])
const copiedStates = ref(false) // 각각의 버튼 상태를 저장할 배열


watch(input1, (newName) => {
    if(changeHistory.value.length > 0){
      if(rdoValType.value === 'date' && input1.value.indexOf('\'') <= -1) {
        resultValue.value = changeHistory.value.at(-1).html.replace('#val', `'${newName}'`)
      } else {
        resultValue.value = changeHistory.value.at(-1).html.replace('#val', newName)
      }
    } else {
      if(rdoValType.value === 'date' && input1.value.indexOf('\'') <= -1) {
        resultValue.value = `'${input1.value}'`
        resultText.value = input1.value
      } else {
        resultValue.value = input1.value
        resultText.value = input1.value
      }
    }
  }
)

const copyToClipboard = () => {
  const text = resultText.value.replace('#val', rdoValType.value === 'date' ? `'${input1.value}'` : input1.value)
  navigator.clipboard.writeText(text).then(() => {
    copiedStates.value = true // 클릭한 버튼의 상태를 true로 설정
    setTimeout(() => {
      copiedStates.value = false // 2초 후 원래 상태로 복원
    }, 2000)
    ElMessage({
      message: '클립보드에 복사되었습니다',
      type: 'success'
    })
  }).catch((err) => {
    console.error('클립보드 복사 실패: ', err)
  });
};

const refresh = () => {
  rdoValType.value = 'procym' //라디오버튼
  input1.value = 'P_PROC_YM'
  changeHistory.value = []
  changeHistoryKR.value = []
  resultValue.value = input1.value
  resultText.value = input1.value
}

//라디오변경
const changeRadio = () => {
  if(rdoValType.value === 'procym') {
    input1.value = 'P_PROC_YM'
    /* eslint-disable no-debugger */
    // debugger
    /* eslint-enable no-debugger */
  } else if (rdoValType.value === 'procdate') {
    input1.value = 'P_PROC_DATE'
  } else {
    
    input1.value = `'${format(today, 'yyyy-MM-dd')}'`
    /* eslint-disable no-debugger */
    // debugger
    /* eslint-enable no-debugger */
  }
}

//날짜 변환
const changeDate = (type) => {
  const result = DateUtil.changeDate(type, resultValue.value, input1.value, 'YYYY-MM-DD')
      resultValue.value = result.toSpan().replace('#val', input1.value)
      resultText.value = DateUtil.changeDate(type, resultText.value, input1.value, 'YYYY-MM-DD').toText()

      changeHistory.value.push({
        html: result.toSpan(),
        plainText: resultText.value
      })

      changeHistoryKR.value.push(type)
}

const undo = () => {
  if(changeHistory.value.length > 1) {
    changeHistory.value.pop()
    changeHistoryKR.value.pop()

    if(rdoValType.value === 'date' && input1.value.indexOf('\'') <= -1) {
      resultValue.value = changeHistory.value.at(-1).html.replace('#val', `'${input1.value}'`)
      resultText.value = changeHistory.value.at(-1).plainText  
    } else {
      resultValue.value = changeHistory.value.at(-1).html.replace('#val', input1.value)
      resultText.value = changeHistory.value.at(-1).plainText  
    }
  } else if (changeHistory.value.length == 1) {
    changeHistory.value.pop()
    changeHistoryKR.value.pop()

    if(rdoValType.value === 'date' && input1.value.indexOf('\'') <= -1) {
      resultValue.value = `'${input1.value}'`
      resultText.value = input1.value
    } else {
      resultValue.value = input1.value
      resultText.value = input1.value
    }
  }
}

</script>

<style>
.el-row{
  margin-bottom: 20px;
}

.el-row:last-child {
  margin-bottom: 0;
}

.el-input{
  width: 400px;
  margin-bottom: 20px;
  margin-right: 20px;
}
</style>