<template>
  <el-container class="layout-container">
    <el-aside 
      :style="{width: isCollapsed ? '60px' : '200px'}"
      :collapse="isCollapsed"
      @mouseenter="isCollapsed=false"
      @mouseleave="isCollapsed=true">
      <el-scrollbar>
        <el-menu 
          :default-openeds="['1']" 
          :default-active="routerPath" 
          :collapse="isCollapsed"
          router>
          <el-sub-menu index="1">
            <template #title>
              <el-icon><EditPen /></el-icon>
              <span>안녕</span>
            </template>
              <el-menu-item v-for="item in menuItems" :key="item.name" :index="item.path" @click="item.clickable ? item.click() : null">
                {{ item.name }}
              </el-menu-item>
          </el-sub-menu>
          
          <el-sub-menu index="3">
            <template #title>
              <el-icon><Setting /></el-icon>
              <span>누르지마세요</span>
            </template>
            <el-menu-item-group>
              <template #title>안 누르기</template>
              <el-menu-item index="없음1" @click="open"><el-icon><Close /></el-icon>없음1</el-menu-item>
              <el-menu-item index="없음2" @click="open"><el-icon><NoSmoking /></el-icon>없음2</el-menu-item>
            </el-menu-item-group>
          </el-sub-menu>
        </el-menu>
      </el-scrollbar>
    </el-aside>

    <el-container>
      <el-header style="text-align: right; font-size: 12px">
        <div class="toolbar">
          <div class="d-flex align-item-center">
            <!-- <el-avatar
            class="mr-3"
            :size="32"
            src="https://raw.githubusercontent.com/jeanguyomarch/pikalogy/master/images/pika_01.png"
            /> -->
            <!-- <el-image class="mr-3" src="https://raw.githubusercontent.com/jeanguyomarch/pikalogy/master/images/pika_01.png"></el-image> -->
            <el-text size="large" tag="b">{{ routerName }}</el-text>
          </div>
          <!-- </div> -->
          <div class="right-content">
            <router-link to="/"> <!--집으로가-->
            <el-link :underline="false" ><el-icon><HomeFilled /></el-icon></el-link>
          </router-link>
          </div>
        </div>
      </el-header>

      <el-main>
        <el-scrollbar>
          <router-view></router-view>
        </el-scrollbar>
      </el-main>
    </el-container>
  </el-container>
</template>

<script setup>
import { ref, watch } from 'vue';
import { useRoute } from 'vue-router';
import { ElMessage } from 'element-plus';

//라우터관련
const route = useRoute()
const routerName = ref()
const routerPath = ref(route.path)

//사이드메뉴
const isCollapsed = ref(true)

watch(
  [() => route.meta.korean, () => route.path], 
  ([newName, newPath]) => {
    routerName.value = newName
    routerPath.value = newPath
  }
)

const open = () => {
  //ElMessage('없음')
  ElMessage({
    message: '없음',
    type: 'error',
    icon: 'apple'
  })
}

const menuItems = [
    {
      name: "따옴표",
      path: "/",
      clickable: false,
    },
    {
      name: "날짜",
      path: "/DateWizard",
      clickable: false,
    },
    {
      name: "😇",
      clickable: true,
      click: open,
    }
  ]

</script>

